@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: width 1s;
  background-color: #fff;
  width: 95%;
  padding: 1rem;
}
.modal .modal-content {
  display: block;
  margin: 1rem 1rem;
}
.modal .modal-content > * {
  margin: 1rem;
}
.modal .modal-content h3 {
  font-size: 2.5rem;
  font-weight: 700;
}
.modal .close-modal:hover {
  cursor: pointer;
}
.modal .close-modal {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  margin: 0;
  padding: 0;
  border: 0;
  background: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.5);
}

@media all and (min-width: 768px) {
  body .container {
    margin-top: 4rem;
  }
  .modal {
    width: 65%;
  }
}
@media all and (min-width: 1024px) {
  body .container .hero {
    flex-direction: row;
    margin: 6rem 0;
    gap: 5rem;
  }
  .modal {
    width: 45%;
  }
  .modal .modal-content > * {
    margin: 1.6rem 2.5rem;
  }
}
* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

h1 {
  font-weight: 700;
  font-size: 2.5rem;
}

p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
}

.show-modal,
.reset-counter {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  background: #e4c1ae;
  color: #fff;
  border: none;
  transition: background 250ms;
  display: block;
}

.reset-counter {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  background: #e4c1ae;
  color: #fff;
  border: none;
  transition: background 250ms;
  display: block;
  padding: 0.7rem 1.75rem;
  font-size: 0.9rem;
}

.show-modal:hover,
.reset-counter:hover {
  cursor: pointer;
  background: #c7a694;
}

.container {
  max-width: 90rem;
  margin: 0 auto;
}
.container .hero {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.container .hero .img {
  max-width: 31rem;
}
.container .hero .img img {
  max-width: 100%;
}
.container .hero .content {
  max-width: 23rem;
  padding: 0.5rem;
}
.container .hero .content > * {
  display: block;
  margin: 1.8rem 0;
}

.hidden {
  display: none;
}